/*стили для разработки*/
a, div, li, nav, ul, header,
footer, figure, form,
label, h1, h2 {
  outline: 1px solid green;
}
body {
  margin: 0;
  padding: 0;
  background: #f2f2f2;
}
/*стили для разработки*/
#app {
  font-family: 'Pt Sans', 'Gerogia', serif;
  font-weight: 700;
  width: 91.5%;
  margin: 0 auto;
  padding: 0;
  background-color: #fefefe;
}

.welcome {
  background: url(/static/img/top-bg.551c1a7.png);
  background-position: 51% 29%;
  background-repeat: no-repeat;
  position: relative;
  height: 509px;
}
.welcome-text {
  position: absolute;
  top: 15%;
  left: 31%;
}
.welcome-decor {
  position: absolute;
  bottom: 0;
}

.main-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  text-transform: uppercase;
}
.main-nav-item {
  padding-top: 18px;
  padding-bottom: 20px;
  font-size: 0.90rem;
  width: 20%;
  text-align: center;
}
.main-nav-item a {
  text-decoration: none;
  color: #000;
}
.main-nav-item:nth-child(3) {
  position: relative;
}
.main-nav-item:nth-child(3) a {
  position: absolute;
  left: center;
  margin-left: -29%;
  min-width: 140px;
  top: -10px;
  height: 70px;
  background: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 0% 0%;
  font-size: 0;
  z-index: 1;
}




































































